* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Bebas Neue", cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #161623;
  position: absolute;
  width: 100%;
  min-height: 100%;
}

body.active {
  position: absolute;
  top: 0;
  right: 300px;
}
.menu {
  position: absolute;
  top: 0;
  right: 0;
  width: 300px;
  min-height: 100vh;
  display: none;
  transition: all 2s ease;
}
.menu.active {
  right: -300px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
body::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: linear-gradient(#093009, #369457);
  clip-path: circle(300px at right 70%);
}

body::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(#1f6032, #29773e);
  clip-path: circle(200px at 10% 10%);
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@media screen and (max-width: 768px) {
  .react-loading-skeleton.skeleton-theme.css-1vmnjpn-skeletonStyles-Skeleton {
    width: 200px !important;
  }
}